@charset "UTF-8";
body {
  background-color: #18242E;
  font-family: "微软雅黑","黑体";
  margin: 0; }

.c1, .c2 {
  position: relative;
  margin-left: 100px;
  display: inline-block; }
  .c1 .sp_title, .c2 .sp_title {
    color: #585e63;
    font-size: 24px; }
  .c1 h2, .c2 h2 {
    text-align: center;
    color: #d0d3d5;
    font-size: 30px;
    line-height: 1;
    margin: 0 0;
    font-weight: normal; }

.c2 .sp_title {
  font-size: 20px;
  margin-left: 30px;
  font-weight: bold; }

.c2 dl {
  font-size: 23px;
  text-align: right; }
  .c2 dl dt i {
    width: 100px;
    height: 3px;
    display: inline-block;
    vertical-align: middle;
    margin-left: 20px; }
  .c2 dl dt.dt-1 {
    color: #4e7ccc; }
    .c2 dl dt.dt-1 i {
      background-color: #4e7ccc; }
  .c2 dl dt.dt-2 {
    color: #36b3c3; }
    .c2 dl dt.dt-2 i {
      background-color: #36b3c3; }
  .c2 dl dt.dt-3 {
    color: #4ecda5; }
    .c2 dl dt.dt-3 i {
      background-color: #4ecda5; }



