#Header {
  display: table;
  width: 100%;
  margin-top: 50px; }
  #Header .item {
    position: relative;
    text-align: center;
    display: table-cell; }
    #Header .item.up-down.up span {
      color: #f34545 !important; }
    #Header .item.up-down.down span {
      color: #19e747 !important; }
    #Header .item.up-down em {
      display: inline-block;
      width: 16px;
      height: 38px;
      background: url(/app/imgs/up.png) no-repeat;
      margin-left: 10px; }
    #Header .item.up-down.down em {
      background-image: url(/app/imgs/down.png);
      margin-bottom: -10px; }
    #Header .item span {
      font-size: 58px;
      color: #ffcb02;
      position: relative; }
      #Header .item span i {
        position: absolute;
        top: -10;
        font-size: 22px;
        color: #fff;
        font-style: normal;
        font-weight: normal; }
    #Header .item p {
      color: #c2d0db;
      font-size: 25px;
      margin: 15px 0; }
    #Header .item s {
      text-decoration: none;
      background-color: #A3B9C9;
      width: 1px;
      height: 30px;
      display: block;
      position: absolute;
      right: 0;
      top: 30px; }



